
          @import "@/styles/_variable.scss";
        
.text-to-sign-lang {
  &--hidden {
    display: none;
  }

  $border-radius: 20px;
  flex: 1;
  gap: 1em;
  display: flex;
  flex-direction: column;

  .input {
    display: flex;
    gap: 1em;
    font-size: 1.2em;
    height: 7em;
    border: none;
    background-color: white;
    border-radius: $border-radius;
    box-shadow: 3px 5px 5px #d6d6d6;

    &__text-field {
      margin: 2%;
      font-size: 1.1em;
      flex: 1;
      border: none;
      resize: none;
      overflow-y: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      background-color: inherit;

      &::-webkit-scrollbar {
        display: none;
      }

      &:focus {
        outline: none;
      }
    }

    &__microphone-button {
      width: 1.5em;
      cursor: pointer;
    }

    &__translate-btn {
      background-color: orange;
      border: none;
      margin: 0;
      font-size: 1.3em;
      width: 2.5em;
      border-radius: 0 $border-radius $border-radius 0;
    }
  }

  .output {
    $unity-background-color: rgba(231, 172, 82, 0.155);
    flex: 1;
    position: relative;
    overflow: hidden;
    background-color: $unity-background-color;
    border-radius: $border-radius;
    box-shadow: 3px 5px 5px #d6d6d6;
    
    &__loading {
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: rgb(239, 233, 219);

      &--hidden {
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s 1s, opacity 1s linear;
      }
    }

    &__avatar {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 100%;
      border-radius: $border-radius;
    }
  }
}