
          @import "@/styles/_variable.scss";
        
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__logo {
    font-size: 1em;
    font-weight: bold;
    color: orange;
    word-spacing: 0.1em;

    > span {
      font-family:'DNFBitBitv2';
    }
  }
  &__size {
    font-size: 2.3em;
    letter-spacing: 0.1em;
  }

  &__bold_font {
    color: orange;
    font-size: 1em;
  }

  &__light_font {
    color: rgba(orange, 0.7);
    font-size: 1em;
  }

  @font-face{
    font-family:'DNFBitBitv2';
    font-style:normal;
    font-weight:400;
    src:url('//cdn.df.nexon.com/img/common/font/DNFBitBitv2.otf')format('opentype')
  }
}